/*jshint esversion: 6 */
import React from "react";
import Select from "react-select-plus";
import * as Styled from "./styled/SearchPlus.js";

export default class App extends React.Component {
	constructor() {
		super();
		this.state = {
			selectedOption: { value: 1 }
		};
	}
	handleChange = selectedOption => {
		this.setState({ selectedOption });
	};
	render() {
		const options = [
			{
				value: 100,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>100</span>
					</span>
				)
			},
			{
				value: 10,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>10</span>
					</span>
				)
			},
			{
				value: 5,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>5</span>
					</span>
				)
			},
			{
				value: 1,
				label: (
					<span>
						<span className="icon-ui-thumbs-up" />
						<span>1</span>
					</span>
				)
			},
			{
				value: -1,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-1</span>
					</span>
				)
			},
			{
				value: -5,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-5</span>
					</span>
				)
			},
			{
				value: -10,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-10</span>
					</span>
				)
			},
			{
				value: -100,
				label: (
					<span>
						<span className="icon-ui-thumbs-down" />
						<span>-100</span>
					</span>
				)
			}
		];
		const { selectedOption } = this.state;
		const value = selectedOption && selectedOption.value;

		const Select = (
			<span>
				<span style={{ color: "green" }}>
					<span className="icon-ui-thumbs-up" />
					<span>1</span>
				</span>
				<span className="icon-top-select" />
			</span>
		);
		return (
			<Styled.SearchPlus>
				<input type="text" placeholder="Search..." />
				<button
					ref="PlusButton"
					type="button"
					onClick={e => {
						console.log("clicked ", this.refs.PlusButton);
						e.preventDefault();
					}}
				>
					{Select}
				</button>
			</Styled.SearchPlus>
		);
	}
}



